const toggleMenuButton = document.getElementById('toggleMenuButton');
const menu = document.getElementById('menu');
const menuOverlay = document.getElementById('menuOverlay');

toggleMenuButton.addEventListener('click', function (){
  menu.classList.toggle('hidden');
  menuOverlay.style.display = menu.classList.contains('hidden') ? 'none' : 'block';
});

// Это позволяет закрыть меню и убрать оверлей, когда кликают на оверлей
menuOverlay.addEventListener('click', function(){
  menu.classList.add('hidden');
  menuOverlay.style.display = 'none';
});

// Также позволяет закрыть меню и убрать оверлей с помощью клавиши Escape
document.addEventListener('keydown', function (event) {
  if (event.key === 'Escape') {
    menu.classList.add('hidden');
    menuOverlay.style.display = 'none';
  }
});


